@charset "UTF-8";
.header-categoty_detail {
  padding-top: 100px; }
  @media (max-width: 540.98px) {
    .header-categoty_detail {
      padding-top: 20px; } }

.header {
  /* Если что убрать */ }
  @media (max-width: 540.98px) {
    .header {
      padding-bottom: 0 !important; } }
  .header .container {
    padding-left: 25px;
    padding-right: 25px; }
  .header__title {
    color: #000;
    margin-bottom: 20px;
    font-size: 43px;
    line-height: 59px; }
    @media (max-width: 767.98px) {
      .header__title {
        margin-left: 0; } }
    @media (max-width: 540.98px) {
      .header__title {
        font-size: 30px;
        margin-bottom: 15px;
        line-height: 35px; } }
  .header-filter {
    margin-left: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 60%; }
    @media (max-width: 1199.98px) {
      .header-filter {
        width: 70%; } }
    @media (max-width: 991.98px) {
      .header-filter {
        width: 80%; } }
    @media (max-width: 540.98px) {
      .header-filter .filter-wrap {
        margin-right: 10px;
        margin-bottom: 10px; } }
    @media (max-width: 767.98px) {
      .header-filter {
        margin-left: 0;
        width: 100%; } }
    @media (max-width: 540.98px) {
      .header-filter {
        flex-wrap: wrap;
        justify-self: space-between; } }
    .header-filter__toggle {
      z-index: 4;
      height: 40px;
      background: #DDDCDC;
      border-radius: 6px;
      display: flex;
      justify-content: space-around;
      align-items: center; }
      .header-filter__toggle .fa {
        margin: 0; }
    .header-filter__menu {
      border-radius: 0;
      z-index: 4;
      /*  background: #DDDCDC;  */
      border: 1px solid #DDDCDC; }
      .header-filter__menu li {
        padding: 7px;
        padding-left: 15px; }
    .header-filter__title {
      font-size: 14px;
      line-height: 16px;
      font-weight: 700;
      color: rgba(0, 0, 0, 0.5);
      margin-bottom: 10px; }
  .header__line {
    width: 100%;
    height: 3px;
    background: linear-gradient(135deg, #3C8CE7 0%, #00EAFF 100%);
    border-radius: 10px;
    margin: 50px 0 50px; }
    @media (max-width: 540.98px) {
      .header__line {
        margin: 30px 0 30px; } }
    @media (max-width: 400.98px) {
      .header__line {
        margin: 20px 0 30px; } }

